* {
  box-sizing: border-box;
}

body {
  color: #212121;
  background-color: #f0fff0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

img {
  max-width: 100%;
  object-fit: cover;
  display: block;
}

.breed-select {
  max-width: 250px;
  margin-top: 50px;
}

.cat-info {
  gap: 25px;
  display: flex;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
}

.loader {
  width: 28px;
  height: 28px;
  box-sizing: border-box;
  background: linear-gradient(0deg, rgba(255, 61, 0, .2) 33%, #ff3d00 100%);
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: relative;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 24px;
  height: 24px;
  background: #263238;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

